    changeFile (event) {
      let me = this
      let file = event.target.files[0]
      let formData = new FormData()
      formData.append('file', file)
      formData.append('teamid', '0')
      formData.append('stepid', '0')
      me.$prompt('请输入限制级密码口令', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /^111\d{3}$/,
        inputErrorMessage: '口令不正确'
      }).then(({ value }) => {
        me.$http.uploadFile('/doproject/output/upload', formData, res => {
          if (res.success) {
            console.log(res.data)
            alert('上传成功了奥')
          } else {
            // 返回错误的处理
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '取消输入'
        })
      })
    }
